import {StyleSheet, Text, TouchableOpacity, View} from 'react-native';
import React, {useEffect} from 'react';

const HomeScreen = ({navigation}) => {
  const onPressMiniApp1 = () => {
    navigation.navigate('MiniApp1');
  };
  const onPressMiniApp2 = () => {
    navigation.navigate('MiniApp2');
  };

  useEffect(() => {}, []);

  return (
    <View style={styles.container}>
      <TouchableOpacity onPress={onPressMiniApp1} style={styles.moduleButton}>
        <Text>App 1</Text>
      </TouchableOpacity>
      <View style={{height: 50}} />
      <TouchableOpacity
        onPress={onPressMiniApp2}
        style={[styles.moduleButton, {backgroundColor: 'red'}]}>
        <Text>App 2</Text>
      </TouchableOpacity>
      <View style={{height: 50}} />
    </View>
  );
};

export default HomeScreen;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  moduleButton: {
    padding: 20,
    borderRadius: 10,
    backgroundColor: 'gray',
  },
});
